html, body {
height: 100%;
overflow-x: hidden;
}
.event-title {
padding: 10px 5px 0px 10px;
}
.event-title p {
font-size: 18px;
font-weight: 400;
margin-bottom: 0;
}

.event {
border: 1px solid rgba(211,211,211,0.9);
border-radius: 3px;

}

.event-page-content {
margin-left: 20%;
}
#my-events {
margin-top: 7%;
}
.events-header {
color: #9b1522;
font-size: 25px;
font-weight: 200;
}

.col-xs-offset-1 {
margin-left: 3%;
}

.col-xs-3 {
width: 25%;
padding-left: 0;
padding-right: 0;
margin-bottom: 35px;
}
.event-time {
padding-left: 10px;
padding-right: 10px;
}
.event-time p{
font-size: 14px;
font-weight: 200;
padding-bottom: 15px;
}

#events-page {
padding-bottom: 100px;
}

#events-search-bar div.col-xs-3 {
margin-bottom: 0;
}

#events-search-bar {
margin-top: 20px;
}

#events-search-bar input.form-control {
border-radius: 20px;
height: 25px;
width: 250px;
}

#events-search-bar i {
position: absolute;
float: right;
top: 5px;
right: 7px;
color: gray;
z-index: 2;
cursor: pointer;
}

#my-events div.col-xs-3,.col-xs-4 {
margin-top: 20px;
margin-bottom: 15px;
}

#all-events div.col-xs-3 {
margin-bottom: 15px;
}



