
.add-event {
  position: fixed;
  left: 0;
  top: 0;
  width: 20%;
  height: 100%;
  background-color: #e6e6e6;


}

.upcoming-events p{
  margin-top: 50px;
  color: #9b1522;
  font-size: 18px;
  font-weight: 500;

}


.past-events p{
  margin-top: 30px;
  font-size: 18px;
  font-weight: 200;
  margin-bottom: 30px;
  color: grey;

}
.new-event button{
  color: white;
  font-size: 12px;
  font-weight: 50;
  background-color: #9b1522;

}

.add-event-content {
  position: fixed;
  top: 10%;
  left: 2%;
}

.new-event button:hover {
  background-color: #9b1522;
  color: white;
}



